import { Component } from '@angular/core';
import { IonicPage, NavController, MenuController, Events } from 'ionic-angular';
import { CommonProvider } from '../../providers/common/common';
import { UserdataProvider } from '../../providers/userdata/userdata';
import { AnalyticsProvider } from '../../providers/analytics/analytics';
import { AuthProvider } from '../../providers/auth/auth';
import { TranslateService } from '@ngx-translate/core';
import { AlertProvider } from '../../providers/alert/alert';

@IonicPage()
@Component({
  selector: 'page-select-outlet',
  templateUrl: 'select-outlet.html',
})
export class SelectOutletPage {
  _chooseMall: any;

  constructor(private navCtrl: NavController,
    private commonProvider: CommonProvider,
    private userdataProvider: UserdataProvider,
    private AuthProvider: AuthProvider,
    private menuController: MenuController, private events: Events,
    private analytics: AnalyticsProvider, private translate: TranslateService,private alertProvider:AlertProvider) {

    this.events.publish('choosemall', true);
    this.analytics.fa.logEvent('SelectOutletPage');
  }

  ionViewWillEnter() {
    this.chooseMall();
    this.menuController.swipeEnable(false);
    if (this.menuController.isOpen())
      this.menuController.close();
  }

  // chooseMall() {
  //   this._chooseMall = this.commonProvider._chooseMall;
  // }

  selectMall(mallData) {
    this.commonProvider.selectMall(mallData);
    setTimeout(() => {
      this.navCtrl.setRoot('HomePage');
    }, 500);
  }



chooseMall() {
  
    this.commonProvider.chooseMalls().subscribe(res => {
      this._chooseMall = res['data'].filter(mall => mall.ismallactive == true);
      
    }, err => {
      let toast = this.alertProvider.presentToast('something went wrong');
      toast.present();
    });

}

}



